.cube_wrapper__2gq86 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}

.cube_container__3ZVKW {
  position: relative;
  width: 300px;
  height: 300px;
  -webkit-transition: -webkit-perspective 300ms ease;
  transition: -webkit-perspective 300ms ease;
  transition: perspective 300ms ease;
  transition: perspective 300ms ease, -webkit-perspective 300ms ease;
}

.cube_cube__suK33 {
  height: 100%;
  width: 100%;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: -webkit-transform 300ms ease;
  transition: -webkit-transform 300ms ease;
  transition: transform 300ms ease;
  transition: transform 300ms ease, -webkit-transform 300ms ease;
  -webkit-transition-duration: 100ms;
          transition-duration: 100ms;
}

.cube_cube__suK33 figure {
  position: absolute;
  margin: 0;
  height: 300px;
  width: 300px;
  display: block;
  opacity: 0.2;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
  border: #979797 solid 0.1rem;
  background: #fff;
  -webkit-animation-name: cube_color_change__2cfV4;
          animation-name: cube_color_change__2cfV4;
	-webkit-animation-duration: 5s;
	        animation-duration: 5s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

.cube_cube__suK33 figure:nth-of-type(1) {
  -webkit-transform: translateZ(150px);
          transform: translateZ(150px);
}
.cube_cube__suK33 figure:nth-of-type(2) {
  -webkit-transform: rotateX(180deg) translateZ(150px);
          transform: rotateX(180deg) translateZ(150px);
}
.cube_cube__suK33 figure:nth-of-type(3) {

  -webkit-transform: rotateY(-90deg) translateZ(150px);

          transform: rotateY(-90deg) translateZ(150px);
}
.cube_cube__suK33 figure:nth-of-type(4) {


  -webkit-transform: rotateY(90deg) translateZ(150px);


          transform: rotateY(90deg) translateZ(150px);
}
.cube_cube__suK33 figure:nth-of-type(5) {

  -webkit-transform: rotateX(90deg) translateZ(150px);

          transform: rotateX(90deg) translateZ(150px);
}
.cube_cube__suK33 figure:nth-of-type(6) {
  -webkit-transform: rotateX(-90deg) translateZ(150px);
          transform: rotateX(-90deg) translateZ(150px);
}

@-webkit-keyframes cube_animateCube__Y7LWj {
  0% {
    -webkit-transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
            transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
  }
  100% {
    -webkit-transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
            transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
  }
}

@keyframes cube_animateCube__Y7LWj {
  0% {
    -webkit-transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
            transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
  }
  100% {
    -webkit-transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
            transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
  }
}

@-webkit-keyframes cube_color_change__2cfV4 {
  0% { box-shadow: inset 10px 10px 87px 89px blue; }
  25% { box-shadow: inset 10px 10px 87px 89px orange; }
  50% { box-shadow: inset 10px 10px 87px 89px yellow; }
  75% { box-shadow: inset 10px 10px 87px 89px black; }
  100% { box-shadow: inset 10px 10px 87px 89px red; }
  /* from { box-shadow: inset 10px 10px 87px 89px  blue; }
  to { box-shadow: inset 10px 10px 87px 89px red; } */
}
@keyframes cube_color_change__2cfV4 {
  0% { box-shadow: inset 10px 10px 87px 89px blue; }
  25% { box-shadow: inset 10px 10px 87px 89px orange; }
  50% { box-shadow: inset 10px 10px 87px 89px yellow; }
  75% { box-shadow: inset 10px 10px 87px 89px black; }
  100% { box-shadow: inset 10px 10px 87px 89px red; }
}

